body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: 100%;
}
.App {
  background-image: url(../public/./assist/images/weather.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  input {
    padding: 15px;
    border: 1px solid transparent;
    border-radius: 15px;
    font-size: 15px;
    background: rgba(250, 250, 250, 0.85);

    outline: 0;
  }
  .weatherresult {
    background: rgba(250, 250, 250, 0.85);

    width: 252px;
    margin-top: 20px;
    border: 1px solid transparent;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    padding: 20px;
    color: #000000eb;
    box-shadow: 10px 10px 5px 0px rgba(15, 15, 15, 0.404);
    text-align: center;
    font-weight: 600;
    .city-temp {
      margin: 22px 0;
    }
    sup {
      background-color: #ff8c00;
      color: #ffff;
      padding: 0 8px;
      font-size: 0.7em;
      border: 1px solid transparent;
      border-radius: 10px;
      display: inline-block;
    }
  }
}
